<template>
  <div id="en">
    <router-view></router-view>
    <button @click="changeLanguageCn">切换中文</button>
  </div>
</template>

<script type="es6">
  //import someComponent from './someComponent'
  export default {
    name: "enhome",
    data() {
      return {
        msg: "Hello Vue.js English"
      }
    },
    methods: {
      changeLanguageCn() {
        let oldPath = this.$route.path
        let newPath = oldPath.replace('en', 'cn')
        window.sessionStorage.setItem('whichLanguage','cn')
        const {href} = this.$router.resolve({
          path: newPath
        })
        window.open(href,'_blank')
      }
    },
    component: {}
  }
</script>

<style scoped>

</style>



